body {
  font-family: "Mont", sans-serif;
  font-size: 18px;
  color: #000;
  font-weight: 400;
}

a, .btn, i, img {
  transition: all 0.3s;
}
a i, .btn i, i i, img i {
  transition: all 0s;
}

.title-decor {
  position: relative;
  padding-left: 2.778em;
}
.title-decor:before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #333333;
  background-image: url("../img/common/title-decor.png");
  max-width: 1.6668em;
  border-radius: 50rem;
  left: 0;
  top: 0;
}

.left-45 {
  left: 2.4999em;
}

.video-wrap {
  aspect-ratio: 1.8;
  background-color: #333333;
  overflow: hidden;
}
.video-wrap iframe {
  width: 100%;
  height: 100%;
}

.bg-line {
  background-size: 50% 50%;
  background-repeat: repeat-x;
  background-position: center;
}

.min-height-50 {
  min-height: 2.778em;
}

.aspect-ratio-1-8 {
  aspect-ratio: 1.8;
}

.aspect-ratio-7-7 {
  aspect-ratio: 7.7;
}

.min-height-typed {
  min-height: 3.8892em;
}

.js-cookie-block {
  background-color: rgba(0, 0, 0, 0.85);
}

.typed-cursor {
  font-size: 2.11128em;
}

.favorites_banner {
  position: fixed;
  bottom: -100%;
  left: 0;
  width: 100%;
  box-shadow: -2px -1px 20px 0px rgba(0, 0, 0, 0.2);
  background: #fff;
  z-index: 10;
  padding: 5px 0;
  transition: all 0.3s;
}

.favorites_banner.active {
  bottom: 0;
  transition: all 0.3s;
}

.right-0 {
  right: 0 !important;
}

.search-link {
  right: 12%;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
}

@media (max-width: 1400px) {
  .search-link {
    right: 13%;
  }

  .min-height-typed {
    min-height: 6.1116em;
  }
}
@media (max-width: 1199px) {
  body {
    font-size: 16px;
  }

  .search-link {
    right: 14%;
  }

  .min-height-typed {
    min-height: 5.556em;
  }
}
@media (max-width: 992px) {
  body {
    font-size: 14px;
  }

  .stage-item {
    margin-bottom: 5.0004em;
  }
  .stage-item:nth-child(3), .stage-item:nth-child(4) {
    margin-bottom: 0;
  }

  .search-link {
    right: 90px;
  }

  .min-height-typed {
    min-height: 5.556em;
  }
}
@media (max-width: 767px) {
  .min-height-typed {
    min-height: 6.1116em;
  }
}
@media (max-width: 576px) {
  .stage-item:nth-child(3) {
    margin-bottom: 5.0004em;
  }

  .h1, h1 {
    font-size: 2em;
  }

  .min-height-typed {
    min-height: 8.334em;
  }
}
.main-header {
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.main-header .logo {
  max-height: 65px;
  width: auto;
}
.main-header .nav-link {
  color: #fff;
  padding: 0.2778em 0.5556em;
  border: 2px solid transparent;
  line-height: 1.2;
  border-radius: 0.2778em;
}
.main-header .nav-link:hover, .main-header .nav-link.active {
  border: 2px solid #d1004a;
}

.search-form button {
  border-radius: 0 1rem 1rem 0;
}

.nav-pills.tab-nav .nav-item {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.nav-pills.tab-nav .nav-link {
  background-color: transparent;
  padding: 0.5556em 1.6668em;
  max-width: 100%;
}
.nav-pills.tab-nav .nav-link.active {
  background-color: #d1004a;
}

@media (max-width: 992px) {
  .nav-pills.tab-nav .nav-item {
    margin-bottom: 0.5556em;
  }
  .nav-pills.tab-nav .nav-item:nth-child(3), .nav-pills.tab-nav .nav-item:nth-child(4) {
    margin-bottom: 0;
  }
  .nav-pills.tab-nav .nav-link {
    padding: 0.2778em 1.1112em;
  }
}
.main-footer .nav-link {
  color: #fff;
  padding: 0;
}
.main-footer .nav-link:hover {
  color: #d1004a;
  text-decoration: underline;
}

